#ember-body-content {
	&.ember-application {
		@import "../../base/main";
		@import "gridstack";
		@import "gridstack-extra";
		@import 'dashboard-gridstack';
		@import 'widget-sidebar';
		@import 'shimmer-loader';
		@import "announcements";
		@import 'trial-notifier';
		@import 'chart';
		@import 'widget/achievement';
		@import 'agent-avail';
		@import 'sample-dashboard';
		@import 'user-onboard';
		@import 'year-in-review-banner';

		.dashboard--cta {
			.btn:last-child {
				margin-right: 0;
			}
		}

		.dashboard {
			background: $app-white;
			min-height: calc(100vh - 67px);
			padding-bottom: 20px;

			.sample-db {
				margin-left: 0;
				margin-right: 0;
				width: auto;
			}

			.page_layout__title-bar .sticky-element {
				box-shadow: none;
				padding: 8px 32px;
				background-color: $app-white;
				min-height: 64px;
				display: flex;
				align-items: center;
			}

			&.dashboard-edit {
				.page_layout__title-bar .sticky-element {
					border-bottom: 1px solid $border-light-color;
				}
			}

			.page_layout__title-bar .page-title {
				width: 100%;
			}

			.page_layout__title-bar .page-title .content-title-container {
				margin: 0;
				width: 100%;
				justify-content: space-between;
				margin-left: 0 !important;

				.info-container {
					margin-left: 0;
					font-size: $text-normal;
					animation: 0.1s fade-in linear;

					.visibility,
					.updated-info {
						padding: 2px 0;
						color: $primary-text-color-1;
						font-weight: $text-weight-semi-bold;
					}

					.visibility {
						.visible-label {
							margin: 0 6px;
						}

						svg {
							width: 16px;
							fill: $secondary-text-color-2;
						}

						.visible-value {
							min-width: 20px;
						}
					}

					.visibility .visible-label,
					.updated-info span {
						font-weight: $text-weight-medium;
						color: #475867; //accessible-color
					}

					.updated-info {
						position: relative;
						margin: 0;
						display: -webkit-inline-box;

						&::before {
							content: '';
							margin-left: 12px;
							padding-left: 12px;
							border-left: 1px solid $border-dark-color;
						}
					}
				}

				.btns-container {
					.btn {
						font-family: inherit;
					}

					.btn-icon svg {
						height: 18px;
					}
				}
			}
		}

		.ember-modal-dialog .sticky-header.activities--header {
			height: auto;
		}

		.page-content.dashboard-widgets-wrapper {
			margin: 0 32px;

			.grid-stack.grid-stack-6 {
				margin-left: -7px;
				margin-right: -7px;
			}

		}

		.page-content.dashboard-widgets-container {
			width: 100%;
			background-color: $app-white;
			margin: 0;
			padding: 16px;

			.grid-stack.grid-stack-6 {
				width: calc(100% - 344px);
				background-color: $app-white;
			}

			.trial_notifier {
				width: calc(100vw - 455px);
			}
		}

		.page-sidebar.widgets-sidebar {
			width: 360px;
			display: inline-block;
			padding: 24px;
			box-sizing: border-box;
			position: fixed;
			right: 0;
			height: calc(100vh - 130px);
			overflow-y: auto;
			min-height: auto;
		}

		.dashboard-empty-wrapper {
			text-align: center;
			margin-top: 10%;

			.widget--empty-icon {
				width: 160px;
				height: 160px;
			}
		}

		.empty-state--content {
			font-size: 16px;
			margin-top: 20px;

			p {
				margin-top: 16px;
			}

			h5 {
				font-weight: $text-weight-semi-bold;
			}
		}


		@import 'widget/leaderboard';
		@import 'widget/scorecard';
		@import 'responsive-screen';

		@keyframes fade-in {
			0% {
				opacity: 0;
			}
			100% {
				opacity: 1;
			}
		}
	}
}