.grid-stack-placeholder .placeholder-content {
	background: url('/a/assets/images/module-dashboard/placeholder-pattern.svg') repeat;
	display: block;
	border-radius: 8px;
	border: none;
}

.ui-draggable-dragging {
	transform: rotate3d(1, 1, 1, 10deg);
}

.dashboard-refresh--icon {
	cursor: pointer;

	svg {
		fill: $linkColor;
	}
}

.gridstack-widget {
	.grid-stack-item-content {
		border-radius: 8px;
		overflow: visible !important;
		z-index: auto !important;
	}

	button.drag-icon, .delete-icon-container {
		border: none;
		background: transparent;
		cursor: pointer;
		position: absolute;
		top: 22px;
		right: 10px;
		z-index: 100;
	}

	.drag-cursor {
		cursor: move;
	}

	.widget-delete-icon,
	.drag-cursor,
	.warning--icon {
		width: 12px;
		height: 12px;
		fill: $btnDisabledColor;
	}

	.info-icon {
		font-size: 12px;
		font-weight: $text-weight-medium;
		top: 24px;
		right: 24px;
	}

	.drag-icon, .delete-icon-container, .info-icon {
		display: inline-block;
		position: absolute;
		top: 18px;
		z-index: 1;

		&.drag-icon {
			left: 8px;
			top: 17px;
		}
		.delete-icon {
			border: none;
			background: none;
		}
		&.info-icon {
			right: 20px;
		}
	}

	.delete-icon-container {
		right: 8px;
	}

	&--item {
		&.widget-config-enabled {
			opacity: 0.5;
		}

		&.active {
			opacity: 1;

			.gridstack-widget-wrap {
				border-color: $linkColor;
			}
		}
	}

	.gridstack-widget-wrap {
		height: 100%;
		border-radius: 8px;
		border: 1px solid $app-border-primary;
		box-shadow: 0px 4px 12px rgba($app-black, 0.06);

		.gridstack-widget--body {
			height: calc(100% - 54px);

			&.widget--loading {
				display: flex;
				justify-content: center;

				.loading {
					align-self: center;
				}
			}
		}

		&:hover {
			box-shadow: 0px 16px 20px rgba($app-black, 0.06);
		}
	}

	.gridstack-widget--item {
		height: 100%;
		background: $app-white;
		box-sizing: border-box;
		width: 100%;
		margin: 0;
		padding: 0;
		border-radius: 8px;
		box-shadow: none;
		border-color: transparent;
		text-align: initial;
		font-family: inherit;

		&:focus-visible {
			border-color: $inputFocusBorder;
		}

		&:hover {
			border-color: transparent !important;
		}

		.sub-text {
			font-size: $text-normal;
			margin-left: 24px;
			color: $secondary-text-color-1;
		}

		.widget-tooltip.ember-tooltip {
			line-height: 18px !important;
			z-index: 9;
		}

		.gridstack-widget--header {
			min-height: 53px;
		}

		&.dashboard-edit {
			.gridstack-widget--body {
				pointer-events: none;
			}

			.widget--title {
				width: calc(100% - 35px);

				.title {
					line-height: 1.4;
				}
			}

			.widget-scorecard	{
				.widget--title {
					width: calc(100% - 28px);
				}

				.warning--msg {
					position: absolute;
					display: inline-block;
					z-index: 6;
					bottom: 19px;
					right: 11px;
				}
			}

			.warning--msg {
				margin-left: 2px;
			}

			.warning--icon {
				width: 14px;
				height: 14px;
			}

			.widget-freshvisuals {
				.freshvisual-overlay {
					position: absolute;
					width: 100%;
					height: 100%;
					top: 0;
				}

				.gridstack-widget--body {
					position: relative;

					iframe {
						width: 99%;
						height: 100%;
						margin-left: 2px;
						pointer-events: none;
					}
				}
			}
		}

		&.dashboard-show {
			.widget--title {
				width: calc(100% - 90px);

				.title {
					width: 100%;

					.widget--title-content {
						max-width: 100%;
						display: inline-block;
						text-overflow: ellipsis;
						white-space: nowrap;
						overflow: hidden;
						vertical-align: middle;
						color: $secondary-text-color-1;

						&.widget--has-warning-msg {
							max-width: calc(100% - 25px);
						}
					}
				}
			}

			.right-wrap {
				display: inline-block;
				width: 118px;
				vertical-align: middle;
				line-height: 18px;
				text-align: right;
				position: absolute;
				top: 8px;
				right: 16px;

				.ember-basic-dropdown {
					position: relative;
					top: 2px;
				}
			}
		}

		.warning--msg {
			position: relative;
			display: inline-block;
			z-index: 6;
			margin-left: 4px;

			.warning--icon {
				width: 16px;
				height: 16px;
				vertical-align: middle;
			}

			&:empty {
				display: none;
			}
		}

		.widget--title {
			width: calc(100% - 60px);
			padding: 15px 0 0 24px;
			margin-left: 0;
			line-height: 17px;
			font-weight: $text-weight-semi-bold;

			.title {
				font-size: $title-small;
				display: inline-block;
				vertical-align: middle;
				line-height: initial;
			}

			&.widget-title-has-threshold {
				width: calc(100% - 60px) !important;
			}

			.widget--title-content {
				word-break: break-word;
			}
		}

		.widget--helptext{
			font-size: $text-normal;
			margin-left: 22px;
			margin-top: 5px;
		}

		.loading {
			width: 16px;
			height: 16px;
			align-self: center;
			background: url(/a/assets/images/blue-spinner.svg) no-repeat center;
			background-position: center;
			background-size: 16px;
			animation: 0.9s linear spin infinite;
		}

		.link {
			border: none;
			background: no-repeat;
			color: $linkColor;
			font-weight: $text-weight-medium;
			cursor: pointer;
		}

		&.sla,
		&.avg-resolution,
		&.avg-first-response {
			.widget-freshvisuals {
				.freshvisuals-header {
					.title {
						font-size: $baseFontSize;
						color: $primary-text-color-1;
						font-weight: $text-weight-semi-bold;
						margin-bottom: 5px;
						line-height: $baseLineHeight;
						width: 88%;
						white-space: nowrap;
						overflow: hidden;
						text-overflow: ellipsis;
					}
					.duration-info {
						font-size: $text-normal;
						font-weight: $text-weight-medium;
						color: $app-form-label-color;
						line-height: 17px;
					}
				}
				.freshvisual_refresh {
					position: absolute;
					bottom: 12px;
					right: 12px;
					background: $background-color;
					border-radius: 4px;
					display: flex;
					align-items: center;
					padding: 5px 8px;
					.refresh-icon {
						width: 12px;
						height: 12px;
					}
					.timer_text {
						display: inline-block;
						color: $alertCancelColor;
						font-weight: $text-weight-medium;
						font-size: $text-normal;
						line-height: 14px;
						padding-left: 5px;
					}
				}
				iframe {
					height: calc(100% - 40px);
				}
				.iframe_overlay {
					height: 100%;
					width: 100%;
					z-index: 10;
					background: $app-grey;
					opacity: 0;
					position: absolute;
					left: 0;
					top: 0;
					display: block;
				}

				.inactive {
					height: 98%;
					width: 98%;
					background: $white;
					border-radius: 8px;
					opacity: 0.8;
					font-weight: $text-weight-semi-bold;
					position: absolute;
					left: 2px;
					top: 2px;
					cursor: default;
					display: flex;
					align-items: center;
					justify-content: center;
					font-size: $baseFontSize;

					.error-msg-container {
						background: $alertWarningBackground;
						border: 1px solid $alertWarningBorder;
						box-sizing: border-box;
						border-radius: 4px;
						color: $app-grey;
						font-weight: $text-weight-normal;
						font-size: $baseFontSize;
						line-height: 20px;
						padding: 10px;
						margin: 25px;
					}
				}
			}
		}
	}

	.widget-container {
		padding: 16px 24px;
	}

	.widget-error-container {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba($app-white, 0.8);
		display: flex;
		align-items: center;
		justify-content: center;

		.widget-error-msg {
			background: $alertWarningBackground;
			border-radius: 4px;
			border: 1px solid $alertWarningBorder;
			padding: 12px;
			max-width: 80%;
		}
	}
}

.grid-stack.grid-stack-animate {
	transition: left 0.12s, top 0.12s, height 0.12s, width 0.12s;
}

.grid-stack > .grid-stack-item {
	$gridstack-columns: 6;
	min-width: (100% / $gridstack-columns);

	@for $i from 1 through $gridstack-columns {
		&[gs-w='#{$i}'] { width: (100% / $gridstack-columns) * $i; }
		&[gs-x='#{$i}'] { left: (100% / $gridstack-columns) * $i; }
		&[gs-min-w='#{$i}'] { min-width: (100% / $gridstack-columns) * $i; }
		&[gs-max-w='#{$i}'] { max-width: (100% / $gridstack-columns) * $i; }
	}

	&.ui-draggable-dragging {
		transition: left 0.12s, top 0.12s, height 0.12s, width 0.12s;

		.grid-stack-item-content {
			box-shadow: none;
			opacity: 1;
		}

		.gridstack-widget--item.widget-config-enabled {
			opacity: 1;
		}
	}

	.grid-stack-item-content {
		inset: 8px;
	}

	&.ui-draggable .title .title--content {
		overflow: initial;
		white-space: initial;
	}
}

.widget--warning-msg {
	display: flex;
	border: none;
	background: transparent;

	.widget-alert-icon {
		width: 14px;
		height: 14px;
		fill: $secondary-text-color-2;
	}
}

// Error message
.widget--error-wrap {
	width: 99%;
	height: 99%;
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	margin-top: 2px;
	margin-left: 2px;
	padding: 0 16px;
	font-size: $title-small;
	font-weight: $text-weight-normal;
	line-height: 1.5;
	border-radius: 8px;
	display: flex;
	align-items: center;
	justify-content: center;
	background: rgba($app-white, 0.6);

	p {
		background: $alertWarningBackground;
		border: 1px solid $alertWarningBorder;
		border-radius: 4px;
		padding: 12px 10px;
		margin-bottom: 0;
	}
}

/* Safari only override */
@media not all and (min-resolution:.001dpcm) {
	@supports (-webkit-appearance:none) {
		.grid-stack.grid-stack-animate, .grid-stack > .grid-stack-item.ui-draggable-dragging {
			transition: left 0s, top 0s, height 0s, width 0s;
		}
	}
}