.dashboard-info-wrapper {
	label {
		font-size: $text-normal;
		font-weight: $text-weight-semi-bold;
	}
}

.dashboard-title input {
	width: 100%;
}

.dashboard-visibility {
	margin: 24px 0;
}

.visible-option-list {
	label.ember-radio-button {
		font-size: $title-small;
		font-weight: $text-weight-normal;
		padding: 4px 0;
	}

	input[type=radio],
	.radio-btn--text {
		display: inline-block;
		vertical-align: middle;
	}
}

.dashboard-base-form .form-field {

	> label {
		margin-bottom: 4px;
	}

	&.radio-field {
		display: flex;
		align-items: center;
		margin: 8px 0;

		label {
			margin: 0;
			padding: 0;
			margin-left: 5px;
			font-weight: $text-weight-normal;
		}
	}

	span.ember-power-select-selected-item {
		@extend .ellipsis;
	}

	.ember-basic-dropdown-content .ember-power-select-option[aria-selected=true] {
		padding-right: 24px;

		&:before {
			transform: translateY(-50%);
			top: 50%;
		}
	}

	&.dashboard-visibility--group {
		.form-field--label {
			display: none;
		}

		.ember-basic-dropdown {
			margin-top: -15px;
			margin-bottom: 24px;
		}
	}
}

.widget-list-title {
	margin-bottom: 24px;

	h2,
	.widget--add-icon {
		display: inline-block;
		vertical-align: middle;
	}

	.widget--add-icon {
		width: 16px;
		height: 16px;
	}

	h2 {
		font-size: $title-small;
		font-weight: $text-weight-medium;
		margin-left: 8px;
	}
}

.widget-list-wrapper {
	font-size: 0;
	width: 312px;

	.widget-item {
		width: 150px;
		height: 150px;
		margin: 4px;
		cursor: pointer;
		text-align: center;
		padding: 24px 18px;
		background: $app-white;
		border-radius: 8px;
		display: inline-block;
		border: 1px solid $border-light-color;
		box-shadow: 0px 2px 5px rgba($app-black, 0.04);
		position: relative;
		font-family: inherit;
		box-sizing: border-box;
		vertical-align: top;

		&:nth-child(odd) {
			margin-left: 0;
		}

		.card--icon {
			width: 50px;
			height: 50px;
			margin: auto;

			.widget--icon {
				width: 100%;
				height: 100%;
			}
		}

		.card--title {
			display: block;
			font-size: $text-normal;
			font-weight: $text-weight-medium;
			margin-top: 16px;
			word-break: break-word;
		}

		.add-widget {
			width: 86%;
			height: 87%;
			position: absolute;
			opacity: 0;
			visibility: hidden;
			margin: auto;
			left: 0;
			right: 0;
			top: 10px;
			display: flex;
			align-items: center;
			border-radius: 6px;
			justify-content: center;
			background-color: $app-sidebar-bg;
			transition: visibility 0s, opacity 0.1s linear;

			.widget--add-icon {
				width: 24px;
				height: 24px;
				border: 2px solid $linkColor;
				fill: $linkColor;
				border-radius: 50%;
				padding: 2px;
			}
		}

		&:hover {
			.add-widget {
				opacity: 0.9;
				visibility: visible;
			}
		}

		&:disabled {
			.card--icon {
				opacity: 0.2;
			}

			&:hover .add-widget {
				display: none;
			}
		}
	}
}

// Widget configurations
.back_to_widgets {
	width: 25px;
	border: none;
	background: transparent;
	text-align: left;
	outline: none;
	vertical-align: top;
	display: inline-block;
	background-color: transparent;
	padding: 2px 6px;
	border-radius: 4px;

	.back-icon {
		display: inline-block;
		vertical-align: middle;
		width: 12px;
		height: 12px;
		position: relative;
		top: -1px;
	}

	&:hover {
		box-shadow: 0px 2px 10px rgba($app-black, 0.13);
	}
}

.widget--title {
	width: 85%;
	vertical-align: middle;
	display: inline-block;
	margin-left: 8px;
	line-height: 1.5;
	font-size: $title-normal;
	font-weight: $text-weight-semi-bold;
	color: $app-form-label-color;
}

.color-panel--item {
	display: inline-block;
	width: 32px;
	height: 32px;
	border: 1px solid $btnBorder;
	box-shadow: 0px 4px 20px rgba($app-black, 0.06);
	border-radius: 6px;
	margin-right: 24px;
	margin-bottom: 14px;
	vertical-align: top;
}

.widget-data--container {
	padding: 26px 0;

	.form-field {
		margin-bottom: 24px;

		input[type=text] {
			width: 100%;
		}

		&--footer {
			.btn {
				width: 100%;
			}
		}

		.ember-power-select-selected-item {
			max-width: 90%;
			// display: inline-block;
			text-overflow: ellipsis;
			white-space: nowrap;
			overflow: hidden;
		}

		&.form-field--has-errors {
			& > input,
			& > textarea,
			& > input[aria-invalid=true]
			.ember-basic-dropdown-trigger {
				border-color: $app-red;
			}

			& > input[aria-invalid=false] {
				border-color: $border-dark-color;
			}
		}

		.form-field--errors {
			position: relative;
			top: -18px;
		}
	}

	.form--errors {
		input[aria-invalid=true] {
			border-color: $app-red;
		}

		input[aria-invalid=false] {
			border-color: $border-dark-color;
		}
	}

	.ember-basic-dropdown .ember-power-select-trigger .ember-power-select-status-icon {
		top: -1px;
	}
}

.scorecard-field--container {
	border-top: 1px solid $btnBorder;
	margin: 0 -16px;
	padding: 24px 20px;

	.scorecard-field--head {
		margin-bottom: 16px;
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
	}

	.form-field--label {
		padding-bottom: 6px;

		.app-icon {
			width: 16px;
			height: 16px;
			vertical-align: middle;
			margin-right: 2px;
		}

		span {
			vertical-align: middle;
		}
	}

	.form-field {
		.form-field--errors {
			top: -4px;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

	.form-field--control {
		width: 61px;
	}

	input {
		&::-webkit-outer-spin-button,
		&::-webkit-inner-spin-button {
			-webkit-appearance: none;
		}

		&[type=number] {
			-moz-appearance: textfield;
		}
	}

	.palette-field {
		.form-field--label {
			border: none;
			font-size: $title-small;
			margin: 0;
			line-height: 1.5;
			color: $secondary-text-color-1;
		}

		&--list {
			min-width: 296px;
			padding-top: 8px;
		}
	}

	.color-panel--item {
		position: relative;

		.selectedtick {
			fill: $app-white;
			background: $linkColor;
			border-radius: 50%;
			width: 13px;
			height: 13px;
			position: absolute;
			top: -5px;
			right: -5px;
		}
	}
}
