.ember-modal-overlay .ember-modal-dialog.user-onboard-info-modal {
	width: 650px;
	top: 40%;

	.close-modal {
		position: absolute;
		top: 16px;
		right: 32px;
		height: 32px;
		width: 32px;
		padding: 0;
		margin: 0;
		background: none;
		border: none;
		box-shadow: none;
	}
}

.user-onboard-info-container {
	display: flex;
	padding: 16px 32px;
	align-items: center;

	.welcome-content {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		min-height: 240px;
		width: calc(100% - 250px);
		padding: 0 4% 0 14px;
		margin: 10% 0;

		.btn {
			background: $btnPrimaryBackground;
			border: none;
			box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.23);
			color: $app-white !important;
			margin: 0 6px;
			width: 90%;
			height: 46px;
		}
	}

	.welcome-text {
		font-size: $title-extra-large;
		line-height: 36px;
	}

	.welcome-info {
		line-height: 24px;
	}

	.welcome-image {
		background: center / contain no-repeat url("/a/assets/images/logos/user-onboarding/welcome-image.svg");
		width: 236px;
		height: 236px;
	}

}
// v2 styles
.ember-modal-overlay .ember-modal-dialog.user-onboard-v2-modal{
	width: 1100px;
	top: 50%;
	background: none;
	box-shadow: none;
}
.user-onboard-v2-info{
	width: 850px;
	background: $app-white;
	padding: 70px 50px;
	box-shadow: 0px 12px 36px rgba(0, 0, 0, 0.5);
	border-radius: 16px;
	margin: -150px auto 0 auto;
	h1{
		font-size: 32px;
		font-weight: 600;
		margin-bottom: 20px;
	}
	p{
		color: $app-form-label-color;
		font-size: 17px;
		line-height: 32px;
	}
	.ember-loader{
		fill: $linkColor;
		width: 50px;
		height: 50px;
	}
	small{
		font-weight: 500;
	}
}
.user-onboard-explore{
	.expore-head{
		background: linear-gradient(115.67deg, #38B8F2 -51.75%, #206DD1 83.77%);
		border-radius: 16px 16px 0 0;
		padding: 24px 36px;
		h4{
			color: $app-white;
			font-size: 16px;
			b{
				font-weight: 500;
			}
		}
	}
	.explore-content{
		background-color: $app-white;
		border-radius: 0 0 16px 16px;
		padding: 36px;
		h2{
			font-size: 24px;
			font-weight: 600;
			margin: -10px 0 8px 0;
		}
		p{
			color: $app-form-label-color;
			font-size: 14px;
		}
		.btn-primary{
			background: $brand-theme-color;
			border: 0;
			border-radius: 4px;
			padding-left: 25px;
			padding-right: 25px;
		}
		.btn-link{
			color: $linkColor !important;
			border: none;
		}
	}
	.explore-list{
		width: 400px;
	}
	.explore-item{
		position: relative;
		border: 1px solid $inputBorder;
		box-shadow: 0px 0px 6px $box-shadow-color-1;
		border-radius: 8px;
		background: $app-white;
		padding: 12px 18px;
		margin-bottom: 12px;
		box-sizing:border-box;
		cursor: pointer;
		&:hover{
			&:not(.disable){
				border-color: $btnDisabledColor;
			}
			span{
				color: $btnDisabledColor;
			}
		}
		&.selected{
			&.disable{
				span{
					color: rgba($app-success-state, 0.5);
				}
				b{
					color: $primary-text-color-1;
				}
			}
			span{
				color: $app-success-state;
			}
		}
		span{
			position: absolute;
			top: 10px;
			left: -12px;
			font-size: 24px;
			color: $inputBorder;
			&:after{
				content: '';
				display: block;
				width: 17px;
				height: 16px;
				border-radius: 50%;
				background: $app-white;
				margin-top: -20px;
			}
		}
		b{
			font-size: 14px;
			font-weight: 500;
			cursor: pointer;
			margin-bottom: 3px;
		}
		p{
			color: $inputHoverBorder;
			font-size: 12px;
			margin: 0;
		}

	}
	.module-image{
		background: center / contain no-repeat url("/a/assets/images/user-onboarding/it_support.png");
		width: 576px;
		height: 350px;
		&.change_management{
			background-image: url("/a/assets/images/user-onboarding/change_management.png");
		}
		&.manage_assets{
			background-image: url("/a/assets/images/user-onboarding/manage_assets.png");
		}
		&.project_management{
			background-image: url("/a/assets/images/user-onboarding/project_management.png");
		}
	}
}
