.announcements-modal {
	.ember-modal-header {
		.title {
			width: 100%;

			.btn {
				margin-left: auto;
			}

			svg {
				width: 14px;
				height: 14px;
			}

			.btn-text {
				margin-left: 5px;
			}
		}

		.announcement-dropdown-wrapper {
			margin-top: 16px;

			.ember-basic-dropdown-trigger {
				font-weight: 500;
				font-size: $title-normal;
				cursor: pointer;
				display: inline;
			}
		}
	}

	.announcement-header {
		margin-bottom: 10px;

		.btn-link {
			padding: 0;
			font-weight: 600;
			margin-left: -16px;
			margin-right: auto;
		}
	}

	.announcement-list-wrapper {
		.announcement-card-wrapper {
			position: relative;
			border: 1px solid $border-light-color;
			margin-bottom: 12px;
			border-radius: 4px;

			&:hover {
				border-color: $linkColor;

				.ember-basic-dropdown {
					opacity: 1;
				}

				.announcement-card {
					border-color: transparent;
				}
			}

			&.active {
				.ember-basic-dropdown {
					opacity: 1;
				}
			}
			body.keyboard-user & .ember-basic-dropdown {
				display: block;
			}
		}

		.announcement-card {
			padding: 16px;
			color: $primary-text-color-1;
			border: 1px solid $border-light-color;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			border-radius: 4px;
			margin: 0px;

			&:focus {
				border-color: $linkColor;

				& + .ember-basic-dropdown {
					opacity: 1;
				}
			}

			.card-title {
				font-size: $baseFontSize;
				font-weight: 600;
			}

			.card-description {
				padding: 14px 0;
				line-height: 1.5;
			}

			.card-footer {
				.announcement-state {
					border-left: 1px solid $border-dark-color;
					margin-left: 16px;
					padding-left: 16px;

					.ember-badge {
						border-color: transparent;

						svg {
							width: 14px;
							height: 14px;
							margin-right: 4px;
						}
					}
				}
			}
		}

		.ember-basic-dropdown {
			position: absolute;
			right: 4px;
			top: 10px;
			opacity: 0;

			&:focus-within {
				opacity: 1;
			}
		}

		.reached-infinity {
			.ember-loader {
				display: none;
			}
		}
	}

	.announcement-empty-state {
		text-align: center;
		line-height: 2;
		color: $secondary-text-color-1;

		.empty-image {
			width: 175px;
			height: 175px;
			margin-top: 20%;
			margin-bottom: 20px;
		}

		.empty-text {
			font-size: $title-normal;
		}

		.btn-text {
			margin-left: 5px;
		}
	}

	.announcement-user {
		.announcement-user-details {
			margin-left: 10px;
			line-height: 1.5;

			.user-details-name {
				font-weight: 500;
			}

			.user-details-createdat {
				font-size: $text-normal;
				color: $secondary-text-color-1;
			}
		}
	}

	.announcement-show-wrapper {
		margin-top: 10px;

		.announcement-field {
			margin-bottom: 26px;
		}

		.announcement-field-label {
			font-size: $text-normal;
			font-weight: 600;
			margin-bottom: 12px;
		}

		.scheduled-date {
			font-weight: 500;
		}

		.field-body {
			line-height: 1.5;
		}
	}

	.ember-form {
		.schedule-text {
			font-weight: 500;
		}
		.schedule-sub-text {
			font-size: $text-normal;
			color: $secondary-text-color-1;
			line-height: 1.8;
		}

		.date-time-split-field {
			position: relative;
			label { display: inline-block !important; }
		}

		.visible-till-hint {
			position: absolute;
			top: 3px;
			margin-left: 5px;
			svg { fill: $linkColor; }
		}

		.autocomplete-field {
			label {
				display: none;
			}

			.ember-power-select-multiple-options {
				min-width: 220px;
				input {
					padding: 0;
				}
			}
		}

		.time-control {
			width: 110px;

			.ember-power-select-dropdown {
				width: 110px;
			}
		}

		.ember-power-select-trigger-multiple-input {
			min-width: 200px;
			padding:0;
		}
	}

	.inline-dialog-box-overlay {
		position: absolute;
		background-color: $app-light-bg;
		width: 100%;
		height: 100%;
		z-index: 10;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;

		.inline-box-content {
			width: 512px;
			min-height: 150px;
			margin: auto;
			margin-top: 30%;
			border-radius: 4px;
			padding: 32px;
			line-height: 1.5;
			border: 1px solid $border-dark-color;
		}

		.inline-box-title {
			font-size: $text-large;
			font-weight: 600;
			margin-bottom: 16px;
		}

		.inline-box-action {
			text-align: right;
			margin-top: 40px;
		}
	}

	.froala-editor-container {
		.fr-toolbar .fr-btn-grp {
			margin: 0 15px 0 12px;
		}

		.fr-wrapper .fr-element {
			min-height: 195px !important;
		}
	}
}

.page-title .announcement-wrapper {
	.btn {
		margin-right: 0;
	}
}


.announcement-banner {
	height: 158px;
	background-color: $app-light-bg;
	border-radius: 8px;
	border: 1px solid $border-light-color;
	box-shadow: 0px 4px 20px 0px rgba(0, 0, 0, 0.06);
	margin-bottom: 16px;
	position: relative;

	&::before {
		position: absolute;
		content: '';
		width: 314px;
		height: 158px;
		background-image: url("/a/assets/images/module-dashboard/announcement-banner.svg");
		background-repeat: no-repeat;
	}

	.banner-title {
		z-index: 0;
		width: 205px;
		display: inline-block;
		font-size: 22px;
		color: $app-light-bg;
		font-weight: $text-weight-semi-bold;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
		margin-top: 52px;
		margin-left: 112px;
	}

	.banner-body {
		width: 100%;
		display: flex;
		flex-wrap: wrap;

		.banner-image {
			margin-right: 10px;
			padding-top: 5px;
		}

		.banner-action {
			width: 120px;
			margin: 20px 24px 20px auto;

			.page-nav-wrap {
				margin-left: 20px;
				display: flex;
				align-items: center;
			}
		}

		.banner-content {
			max-width: calc(100% - 560px);
			padding: 17px 0;
			margin-left: 52px;

			.banner-text {
				flex: 0 0 100%;
				display: none;

				&.active {
					display: block;

					/* for chrome and safari*/
					-webkit-animation-duration: 0.5s;
					-webkit-animation-name: slidein;

					/*for firefox*/
					-moz-animation-duration: 0.5s;
					-moz-animation-name: slidein;

					/* for opera*/
					-o-animation-duration: 0.5s;
					-o-animation-name: slidein;

					/* Standard syntax*/
					animation-duration: 0.5s;
					animation-name: slidein;
				}

				.btn-link {
					svg {
						margin-right: 4px;
					}
				}

			}

			.mark-as-read {
				margin-left: 34px;
			}

			.banner-content-title {
				font-weight: 600;
				a {
					color: $primary-text-color-1;

					&:hover {
						color: $linkColor;
					}
				}
			}

			.banner-sub-title {
				font-size: $text-normal;
			}

			.banner-created {
				color: $secondary-text-color-1;
				margin-left: 18px;
			}

			.banner-user {
				position: relative;

				&::after {
					position: absolute;
					content: "";
					border: 2px solid $border-dark-color;
					border-radius: 5px;
					top: 6px;
					margin-left: 5px;
				}
			}
		}

		.banner-description {
			margin: 8px 0 4px;
			line-height: 1.5;

			@include line-clamp($lines: 2);
		}
	}
}

// TODO: NEED TO REMOVE / REVISTT
.app-main-wrapper, .ember-wrapper {
	.btns-container, .ember-modal-header {
		.btn-icon {
			padding: 4px 12px!important;
		}

		.btn-text {
			margin-left: 5px;
		}
	}
}

@-webkit-keyframes slidein {
	from {
		margin-left: 5%;
	}

	to {
		margin-left: 0%;
	}
}

@-moz-keyframes slidein {
	from {
		margin-left: 5%;
	}

	to {
		margin-left: 0%;
	}
}

@-o-keyframes slidein {
	from {
		margin-left: 5%;
	}

	to {
		margin-left: 0%;
	}
}

@keyframes slidein {
	from {
		margin-left: 5%;
	}

	to {
		margin-left: 0%;
	}
}
