.widget-scorecard, .widget-approval {
	position: relative;

	.warning--icon {
		width: 16px;
		height: 16px;
		vertical-align: middle;
		fill: $secondary-text-color-2;
	}

	.warning--msg {
		display: inline-block;
	}

	&.widget--error {
		.score-card-details {
			opacity: 0.3;
		}

		.gridstack-widget--header {
			opacity: 0.8;
		}
	}

	.score-card-details {
		padding: 14px;
		height: 100%;
		box-sizing: border-box;
		display: block;

		.scorecard-count {
			display: block;
			font-size: 32px;
			color: $linkColor;
			margin-left: 8px;
			margin-top: 6px;
			font-weight: $text-weight-bold;
		}
	}

	&.alert-card,
	&.critical-card {
		.title {
			color: $primary-text-color-1;
		}

		.alert-icon,
		.critical-icon {
			display: inline-block;
			position: absolute;
			top: 12px;
			right: 12px;
			background: $white;
			border-radius: 6px;
			padding: 3px 4px 0px;
			width: 32px;
			height: 32px;
			text-align: center;
			box-sizing: border-box;

			.app-icon {
				height: 20px;
				width: 20px;
				display: inline-block;
				position: relative;
				top: 2px;
			}
		}

		.alert-icon {
			padding-top: 4px;
		}

		.scorecard-tooltip {
			font-size: $text-normal;
			background: $app-grey;
			top: 8px !important;
			border-radius: 4px;

			.ember-tooltip-arrow {
				display: none;
			}
		}

		.widget-delete-icon,
		.drag-cursor,
		.warning--icon {
			fill: $secondary-text-color-2;
		}
	}

	.widget--error-wrap {
		margin-top: 1px;
		margin-left: 1px;
		padding: 0 12px;
		line-height: 1.3;

		p {
			padding: 8px;
		}
	}
}

.widget-scorecard,
.widgets-sidebar .palette-field .color-panel--item {
	border-radius: 8px;

	&.lavender {
		background: $lavender;
		border: 1px solid $border-light-blue;

		.scorecard-count {
			color: $app-primary;
		}
	}

	&.white {
		background: $white;
		border: 1px solid $border-light-color;
		border: 1px solid $border-dark-color;

		.scorecard-count {
			color: $app-primary;
		}
	}

	&.humming-bird {
		background: $app-jade-light;
		border: 1px solid $hummingBirdGreenBorder;

		.scorecard-count {
			color: $app-green;
		}
	}

	&.solitude {
		background: $background-color-2;
		border: 1px solid $border-dark-color;

		.scorecard-count {
			color: $app-grey;
		}
	}

	&.alice-blue {
		background: $aliceBlue;
		border: 1px solid $aliceBlueBorder;

		.scorecard-count {
			color: $aliceBlueText;
		}
	}

	&.deep-sky-navy-blue {
		background: $deepSkyNavyBlue;
	}

	&.violet-light-slate-blue {
		background: $violetLightSlateBlue;
	}

	&.lime-yellow-bahia-green {
		background: $limeYellowBahiaGreen;
	}

	&.san-marino-danube-blue {
		background: $sanMarinoDanubeBlue;
	}

	&.ferra-sandrift-brown {
		background: $ferraSandriftBrown;
	}

	&.deep-sky-navy-blue,
	&.violet-light-slate-blue,
	&.lime-yellow-bahia-green,
	&.san-marino-danube-blue,
	&.ferra-sandrift-brown {
		border: 1px solid transparent;

		.widget--title .title,
		.scorecard-count {
			color: $white;
		}

		.widget-delete-icon,
		.drag-cursor,
		.warning--icon {
			fill: $white;
		}
	}

	&.alert-card {
		background: $app-casablanca-light;
		border: 1px solid $alertPartialBorder;

		.widget--title .title {
			color: $app-grey;
		}

		.scorecard-count {
			color: $alertText;
		}
	}

	&.critical-card {
		background: $alertWarningBackground;
		border: 1px solid $alertWarningBorder;

		.widget--title .title {
			color: $app-grey;
		}

		.scorecard-count {
			color: $criticalText;
		}

		.critical-icon {
			padding-top: 3px;
		}
	}

	&.alert-card,
	&.critical-card {
		.widget-delete-icon,
		.drag-cursor,
		.warning--icon {
			fill: $secondary-text-color-2;
		}
	}
}

.scorecard {
	.loading {
		display: none;
	}
}

.deep-sky-navy-blue,
.violet-light-slate-blue,
.lime-yellow-bahia-green,
.san-marino-danube-blue,
.ferra-sandrift-brown {
	&.delete-icon .app-icon {
		fill: $white;
	}

	&.critical-card.delete-icon,
	&.alert-card.delete-icon {
		.app-icon {
			fill: $secondary-text-color-2;
		}
	}

	&.gridstack-widget-wrap.widget--error {
		border: none;

		.widget--error-wrap {
			width: 100%;
			height: 100%;
			margin-top: 0px;
			margin-left: 0px;
		}
	}
}

.gridstack-widget-wrap.widget-scorecard {
	transition: all .35s;

	.gridstack-widget--header {
		.drag-icon {
			top: 15px;
		}
	}

	.gridstack-widget--body {
		justify-content: initial;
	}
}

.dashboard-edit {
	.gridstack-widget-wrap.widget-scorecard, .gridstack-widget-wrap.widget-approval {

		.title .widget--title-content {
			@include line-clamp($lines: 2);
		}

		.gridstack-widget--body {
			height: auto;	
			.score-card-details {
				height: auto;
			}
		}
	}
}

.gridstack-widget .gridstack-widget--item.dashboard-show {
	.gridstack-widget--body {
		&.score-card--body {
			width: 100%;
			display: block;
			height: 100%;
			position: absolute;
			top: 0;
		}

		.score-card-details {
			.scorecard-count {
				margin-top: 55px;
			}
			.approval-widget-count {
				margin-top: 0;
			}
		}
	}

	.gridstack-widget-wrap.widget-scorecard:hover {
		transform: scale(1.01);
	}

	.widget-scorecard .widget--title,
	.widget-approval .widget--title {
		width: calc(100% - 28px);
		position: relative;
		z-index: 2;
	}

	.widget-scorecard {
		&.deep-sky-navy-blue,
		&.violet-light-slate-blue,
		&.lime-yellow-bahia-green,
		&.san-marino-danube-blue,
		&.ferra-sandrift-brown {
			.widget--title .title .widget--title-content,
			.scorecard-count {
				color: $white;
			}

			.widget-delete-icon,
			.drag-cursor,
			.warning--icon {
				fill: $white;
			}
		}

		&.alert-card {
			.widget--title .title .widget--title-content {
				color: $app-grey;
			}

			.scorecard-count {
				color: $alertText;
			}
		}

		&.critical-card {
			.widget--title .title .widget--title-content {
				color: $app-grey;
			}

			.scorecard-count {
				color: $criticalText;
			}
		}
	}
}

.gridstack-widget--item.active {
	.gridstack-widget-wrap {
		&.lavender {
			border-color: darken($border-light-blue, 15%);
		}

		&.white {
			border-color: darken($border-dark-color, 15%);
		}

		&.humming-bird {
			border-color: darken($hummingBirdGreenBorder, 15%);
		}

		&.solitude {
			border-color: darken($border-dark-color, 15%);
		}

		&.alice-blue {
			border-color: darken($aliceBlueBorder, 15%);
		}

		&.alert-card {
			border-color: darken($alertPartialBorder, 15%);
		}

		&.critical-card {
			border-color: darken($alertWarningBorder, 15%);
		}

		&.deep-sky-navy-blue,
		&.violet-light-slate-blue,
		&.lime-yellow-bahia-green,
		&.san-marino-danube-blue,
		&.ferra-sandrift-brown {
			border-color: transparent;
		}
	}
}
