.widget-achievement {
	.achievement-details {
		margin: 16px 26px;
	}

	.filtered-by {
		color: $secondary-text-color-1;
		font-size: 12px;
	}

	.highcharts-root {
		height: 180px;
		width: 180px;

		.highcharts-point.highcharts-color-0 {
			fill:$highlight-green !important;
		}

		path.highcharts-point.highcharts-color-1 {
			fill: $highlight-grey !important;
		}
	}

	.achievement-data {
		text-align: center;
		overflow: hidden;

		.highcharts-wrapper {
			padding: 0 8px;
		}

		.achievement-no-data {
			img {
				width: 40px;
				height: 45px;
			}

			.achievement-no-data-text {
				color: $secondary-text-color-2;
				margin: 0 10px;
				width: 200px;
			}
		}

		.achievement-no-agent-data {
			margin-top: 50px;

			.achievement-no-agent-text {
				color: $secondary-text-color-2;
			}
		}

		.info-wrapper {
			.arcade-info {
				text-align: left;

				span {
					font-weight: 500;
					margin-bottom: 3px;
				}
			}
		}

		.badge-wrapper {
			padding: 0px 24px;
			justify-content: space-evenly;

			.badge-items {
				padding: 4px 16px;
				max-width: 200px;
				word-break: break-word;

				.achievement-badge-name {
					margin-top: 10px;
					max-height: 70px;
					overflow: hidden;
					display: -webkit-box;
					-webkit-line-clamp: 3;
					-webkit-box-orient: vertical;
				}

				.quest-badges {
					width: 40px;
					height: 45px;
				}

				span {
					width: 110px;
					margin-top: 10px;
				}
			}
		}
	}

	.achievement-no-agent-data {
		text-align: center;
		padding: 32px 24px;
	}
}

.gridstack-widget .dashboard-edit.achievement {
	.badge-items {
		min-width: 80px !important;
	}

	.show_all{
		display: none;
	}
}