.sample-data-banner {
	border-radius: 4px;
	display: flex;
	align-items: center;
	height: 36px;
	width: 100%;
	margin: 0 16px;

	.alert-icon-container {
		height: 24px;
		padding: 4px 8px;
		border-right: 1px solid $alertPartialBorder;
	}

	.alert-icon {
		fill: $app-warning-state;
		height: 16px;
		width: 16px;
	}

	p {
		margin: 0 10px;
	}

	.btn-link {
		border: 0;
		padding: 0;
	}

}

.dashboard_preview {
	width: 100%;
}

.demo-video-container {
	background-color: $dropdown-content-border !important;
	padding: 32px;
	top: 42% !important;
	width: 900px !important;

	.wistia-container {
		padding: 56.25% 0 0 0;
		position: relative;
	}

	.wistia-responsive-wrapper {
		height: 100%;
		width: 100%;
		position: absolute;
		left: 0;
		top: 0;
	}

	iframe {
		height: 100%;
		width: 100%;
	}
}
