.gridstack-widget {
	.gridstack-widget--item .widget-leaderboard {
		.leaderboard-details {
			margin: 24px;
		}
		.filtered-by {
			color: $secondary-text-color-1;
			font-size: 12px;
		}
		.leaderboard-no-data {
			text-align: center;
			padding: 0 52px;

			img {
				height: 108px;
				width: 108px;
			}
			p {
				color: #475867; //accessible-color
				margin: 0;
			}
		}
		// Update wait time for later in slide function as well (widget-leaderboard/component.js)
		$animation-duration: .35s;
		.leaderboard-data {
			text-align: center;
			overflow: hidden;
			padding: 6px 20px;

			.cards {
				display: flex;
				// height: calc(100% - 50px);
				justify-content: center;
				position: relative;
				&.cards-5 {
					width: 160%;
				}
				&.slide {
					transition: right $animation-duration;
				}
				&.card-position-1 {
					right: -9%;
				}
				&.card-position-2 {
					right: 30%;
				}
				&.card-position-3 {
					right: 69%;
				}
				&.cards-2 {
					.leaderboard-user {
						margin: 0 auto;
					}
				}
			}
			.leaderboard-user {
				border: 1px solid transparent;
				background-color: transparent;
				cursor: pointer;
				display: inline-flex;
				align-items: center;
				flex-direction: column;
				flex-shrink: 0;
				justify-content: center;
				margin: 2px 0;
				opacity: 0.5;
				width: 24.5%;
				transform: scale(0.8);
				transition: all $animation-duration;

				@media screen and (max-width: 1100px) {
					opacity: 0;
				}
				&.current {
					&:hover {
						transform: scale(1.05);
					}
					color: inherit;
					transform: scale(1);
					opacity: 1;
					width: 23%;
				}
				&:not(.current) {
					&:hover {
						transform: scale(0.83);
					}
					.category {
						font-size: 12px;
					}
				}
				.user-card-avatar {
					position: relative;
				}
				.leaderboard-item-image {
					left: unset;
					top: 52px;
					right: -38px;
				}
				.user-name {
					font-size: 16px;
					font-weight: 500;
					margin: 8px 0;
					max-width: 135px;
				}
				.category {
					border-radius: 20px;
					color: $app-white;
					font-size: 12px;
					font-weight: 600;
					padding: 0 16px;
					height: 20px;
					max-width: 170px;
				}
				.category-mvp {
					background-color: $backgroundColorCard1;
				}
				.category-love {
					background-color: $backgroundColorCard2;
				}
				.category-sharpshooter {
					background-color: $backgroundColorCard3;
				}
				.category-speed {
					background-color: $backgroundColorCard4;
				}
			}
			.navigate {
				margin-top: 12px;
				button {
					border: none;
					background: none;
					line-height: normal;
					padding: 0;
					height: 24px;
					width: 24px;
					margin: 0 8px;
					svg {
						height: inherit;
						width: inherit;
						fill: #92A2B1;
					}
					&:hover {
						svg {
							fill: $primary-text-color-1;
						}
					}
				}
			}
		}

		&.dashboard-edit {
			.leaderboard-details {
				margin: 16px 20px;
			}

			.leaderboard-user {
				transform: scale(0.7);
			}
			.cards {
				pointer-events: none;
			}
		}
	}
}
