.trial_notifier {
	background-color: $section-background-blue;
	border: 1px solid $border-light-blue;
	border-radius: 4px;
	box-sizing: border-box;
	display: flex;
	align-items: center;
	height: 48px;
	padding: 8px 26px;
	margin-bottom: 16px;

	p {
		margin: 0;
	}

	.trial_notifier_actions, .trial_notifier_contact {
		align-items: center;
		display: inline-flex;
	}

	.trial_notifier_actions {
		font-weight: 600;
		padding-left: 4px;

		a {
			margin-right: 16px;
		}

		.trial_notifier_chat {
			background-color: $app-white;
			border: none;
			border-radius: 4px;
			box-shadow: none;
			color: $linkColor !important;

			&:hover {
				box-shadow: 0 0 0 1px $linkColor;
				-webkit-box-shadow: 0 0 0 1px $linkColor;
				-moz-box-shadow: 0 0 0 1px $linkColor;
			}

			&:focus-visible {
				border-radius: 2px;
			}
		}
	}

	.trial_notifier_contact {
		font-weight: 500;
		margin-left: auto;

		.trial_notifier_call_section {
			&::before {
				content: '';
				border: 2px solid $disabled-text-color;
				border-radius: 4px;
				display: inline-block;
				margin: 0 8px;
				vertical-align: middle;
			}

			a {
				color: inherit;
			}
		}

	}

}
