// CSS For Year in Review Container

@mixin keyframes($name){
	@keyframes #{$name} {
		@content;
	}
	@-ms-keyframes #{$name} {
		@content;
	}
	@-webkit-keyframes #{$name} {
		@content; 
	}
	@-moz-keyframes #{$name} {
		@content;
	}
}

@mixin prefix($property, $value, $prefixes: ()) {
	@each $prefix in $prefixes {
		#{'-' + $prefix + '-' + $property}: $value;
	}
	// Output standard non-prefixed declaration
	#{$property}: $value;
}

.year-in-review {
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	background-color: white;
	padding-top: 15px;
	.review-wrapper {
		position: relative;
		background-color: $banerBackground;
		padding: 17px 20px;
		color: $white;
		height: 110px;
		margin: 0 32px;
		border-radius: 8px;
		&::before {
			content: "";
			position: absolute;
			height: 110px;
			max-width: 315px;
			top: 0px;
			right: 0px;
			left: 0px;
			background-image: url("/a/assets/images/logos/YIR_Banner.svg");
			background-repeat: no-repeat;
			background-size: 100% 100%;
			border-radius: 8px;
		}
	}
	.review-block {
		float: left;
		margin-left: 220px;
		max-width: 55%;
		z-index: 1;
		position: relative;
	}
	.trophy-icon {
		width: 100px;
		height: 70px;
	}
	.watch-btn {
		float: right;
		margin-top: 22px;
		margin-left: 20px;
		.btn-primary {
			background: $navbarBackgroundHighlight;
			border-radius: 15px;
			padding-top: 5px;
			padding-left: 15px;
			padding-right: 15px;
			color: black !important;
			font-weight: bold;
			font-size: 12px;
			border: none;
		}
		.ficon-play-circle {
			margin-left: 4px;
			color: $banerBackground !important;
		}
		.watch-video-btn {
			display: flex;
			display: -ms-flexbox;
			justify-content: center;
			align-items: center;
		}
	}
	.review-desc {
		margin-bottom: 0px;
		margin-top: 10px;
		font-size: 14px;
		line-height: 1.4;
		min-width: 100%;
		overflow-wrap: break-word;
		word-wrap: break-word;
		hyphens: auto;
	}
	.review-title {
		font-size: 20px;
		font-weight: bold;
	}
}

@media (max-width: 1270px) {
	.year-in-review {
		.review-block {
			margin-left: 220px;
			max-width: 50%;
			z-index: 1;
			position: relative;
		}
		.watch-btn {
			margin-left: 20px;
		}
	}
}
@media (min-width: 1350px) {
	.year-in-review {
		.watch-btn {
			margin-top: 22px;
		}
	}
}

// CSS For Ember Video Player
.year-in-video-container {
	position: absolute;
	margin: auto !important;
	left: 10px !important;
	right: 10px !important;
	top: 15px !important;
	width: 840px !important;
	transform: none !important;

	.ember-modal-header {
		padding-top: 10px;
	}
	.ember-modal-body {
		padding: 0px 40px !important;
		max-height: 95% !important;
		height: 100% !important;
		text-align: center;
		border: 0;
		.video-player {
			height: 430px;
		}
		.video-js {
			width: 100%;
			height: 100%;
		}
		.ficon-info_icon {
			margin-right: 5px;
		}
		.download-link {
			padding: 21px 0;
			#downloadYIRVideo {
				font-weight: bold;
				text-decoration: underline;
			}
		}
	}

	.download-progress {
		padding: 21px 0;

		.downloading-text {
			color: #2C5CC5;
			font-weight: bold;
		}
		.download-percent {
			margin-left: 5px;
		}
		span {
			&:nth-child(n+2):nth-child(-n+4) {
				@include prefix(animation-name, opacity, webkit ms moz);
				@include prefix(animation-duration, 1s, webkit ms moz);
				@include prefix(animation-iteration-count, infinite, webkit ms moz);
				@include keyframes(opacity) {
					0% { opacity: 1; }
					100% { opacity: 0; }
				}
			}
			
			&:nth-child(3) {
				@include prefix(animation-delay, 100ms, webkit ms moz);
			}
			
			&:nth-child(4) {
				@include prefix(animation-delay, 100ms, webkit ms moz);
			}
		}
	}
}
