@media (min-width: 1921px) {
	.dashboard-show {
		.page-content.dashboard-widgets-wrapper,
		.page-content.sample-db {
			width: 70%;
			max-width: 1890px;
			margin: 0 auto;
		}
	}

	.dashboard-edit {
		.page-content.dashboard-widgets-container {
			padding-right: 360px;

			.grid-stack.grid-stack-6, .trial_notifier  {
				width: 70%;
				max-width: 1890px;
				margin-left: auto;
				margin-right: auto;
			}
		}
	}

	.dashboard-shimmer .shimmer-loader-container,
	.page-content.quest-details-list {
		width: 70%;
		max-width: 1890px;
		margin: 0 auto;
	}
}

@media (max-width: 1460px) {
	.dashboard-edit {
		.widget-scorecard .widget--error-wrap {
			font-size: 13px;
			padding: 0 5px;
		}

		&.gridstack-widget--item .widget-leaderboard .leaderboard-data .leaderboard-user .category {
			max-width: 120px;
		}
	}
}

@media (max-width: 1620px) {
	.gridstack-widget .gridstack-widget--item.dashboard-show .widget-barchart .widget--title {
		width: calc(100% - 120px);
	}

	.gridstack-widget .gridstack-widget--item.dashboard-show .widget-scorecard .widget--title {
		width: calc(100% - 28px);
	}
}