.gridstack-widget .gridstack-widget--item {
	.ember-basic-dropdown {
		display: inline-block;
	}

	.ember-basic-dropdown-trigger {
		padding: 0;
		margin: 0;
		width: 32px;
		height: 32px;
		display: flex;
		align-items: center;
		justify-content: center;

		svg {
			height: 16px;
		}
	}

	.ember-basic-dropdown-content {
		min-width: auto;
		text-align: center;

		button {
			min-width: 96px;
			text-align: center;
			padding: 6px;
			border-radius: 4px;
			font-size: 14px;
			color: black;

			&:hover {
				background: $dropdown-option-selected;
			}
		}
	}

	.highcharts-wrapper {
		padding: 24px;
	}

	.achievement-data .highcharts-wrapper {
		padding: 0;
	}

	.chart-container svg {
		width: 100%;
		height: 100%;
	}

	.highcharts-xaxis-labels span {
		white-space: nowrap !important;
	}

	button.show-table {
		display: none;
	}

	.no-data {
		background: url(/a/assets/images/empty-states/no-dashboard-chart-data.svg);
		height: calc(100% - 100px);
		padding: 200px 24px 0;
		box-sizing: border-box;
		text-align: center;
		background-repeat: no-repeat;
		background-position: center 50px;
		background-size: 120px;
	}

	.widget-barchart .highcharts-wrapper {
		margin: 3% auto 0;
	}

	.widget-piechart .gridstack-widget--body .highcharts-data-table {
		display: none !important;
	}
}

.ember-modal-dialog.bar-chart-modal, .ember-modal-dialog.pie-chart-modal {
	width: 800px;

	.title {
		font-size: 22px;
		font-weight: $text-weight-semi-bold;
	}

	.ember-modal-body {
		padding-top: 12px;
		height: calc(100% - 125px);
		overflow-y: auto;
	}

	.chart-container {
		width: 540px;
		svg {
			width: 100%;
			height: 100%;
		}
	}

	.highcharts-xaxis-labels span {
		white-space: nowrap !important;
	}

	&.table-only .chart-container {
		display: none;
	}

	table {
		width: 100%;
		text-align: left;

		caption {
			display: none;
		}

		thead th {
			background: $app-sidebar-bg;
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
			width: 50%;
			padding: 12px;
		}

		thead th:last-child {
			border-top-right-radius: 4px;
			border-bottom-right-radius: 4px;
		}

		thead th:first-child {
			border-top-left-radius: 4px;
			border-bottom-left-radius: 4px;
		}

		tbody td {
			font-size: $baseFontSize;
			font-weight: $text-weight-semi-bold;
		}

		th,
		td {
			margin: 0;
			font-size: $baseFontSize;
			padding: 16px;
		}

		tbody {
			th, td {
				border-bottom: 1px solid $border-light-color;
			}
		}

		th:last-child,
		td:last-child {
			transform: translateX(-2px);
		}
	}
}

.widget-piechart, .pie-chart-modal {
	.widget-donut-title {
		color: $primary-text-color-1;
		display: inline-block;
		text-align: center;
		font-size: $baseFontSize;
		font-weight: $text-weight-medium;
		max-width: 100px;

		b {
			font-size: 22px;
			font-weight: $text-weight-bold;
		}
		.widget-donut-module {
			display: block;
			white-space: normal;
		}
	}
	.highcharts-tooltip {
		strong {
			color: $secondary-text-color-1;
			font-size: $title-large;
		}
	}
	.highcharts-empty-series {
		display: none;
	}
}

.widget-pie-count {
	font-weight: $text-weight-semi-bold;
}

.widget-pie-tooltip-point {
	display: block;
	font-size: $title-large;
	margin-top: 8px;
}
