.gridstack-widget .gridstack-widget--item.agent-availability {
	// padding: 24px;
	// color: $app-grey;

	.widget-header {
		display: flex;
		padding: 0;

		.title-container {
			flex: 22;
			max-width: 85%;

			.title {
				width: auto;
				max-width: calc(100% - 32px);
				display: inline-block;
				vertical-align: middle;
			}

			.warning-message {
				display: inline-block;
				vertical-align: middle;
				margin-left: 4px;
				height: auto;

				svg {
					width: 12px;
					height: 12px;
					fill: $secondary-text-color-2;
					margin-top: -1px;
					display: block;
				}
			}

			.warning-tooltip {
				max-width: none;
				width: 280px;
				font-size: 12px;
				line-height: 20px;
				background: $app-grey;
				padding: 8px;
				box-sizing: border-box;
				border-radius: 4px;
				color: $app-white;
				text-align: center;
				z-index: 10;
			}
		}

		// .btn-group.more-info{
		// 	flex: 4;
		// 	align-self: flex-start;

		// 	button {
		// 		font-weight: 500;
		// 		font-family: inherit;
		// 		padding: 4px;
		// 		margin-right: -4px;
		// 		margin-top: -4px;
		// 		float: right;
		// 	}
		// }
	}

	.btn-group.more-info {
		position: absolute;
		top: 10px;
		right: 12px;
	}

	&.dashboard-edit .widget-header .title {
		max-width: 100%;
	}

	.sub-text {
		color: $secondary-text-color-1;
		font-size: 12px;
		margin-top: 4px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}

	.agent-availability-container {
		width: 100%;
		// height: calc(100% - 50px);
		display: flex;
		justify-content: space-around;
		margin: 40px 0px;

		button:hover span {
			color: $secondary-text-color-1;
		}

		button span, button:disabled span {
			color: $app-grey;
		}

		.availability-count {
			display: block;
			font-size: 30px;
			font-weight: $text-weight-semi-bold;
			padding: 16px;
		}

		.show {
			cursor: pointer;
		}

		span {
			display: inline-block;
			vertical-align: middle;
			font-size: 14px;
		}

		svg {
			width: 16px;
			height: 16px;
			margin-right: 8px;
		}
	}

	.inactive-widget .widget-header {
		.more-info, .sub-text {
			display: none;
		}
	}
	.inactive {
		height: 98%;
		width: 98%;
		background: $white;
		border-radius: 8px;
		opacity: 0.8;
		font-weight: $text-weight-semi-bold;
		position: absolute;
		left: 2px;
		top: 2px;
		cursor: default;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: $baseFontSize;

		.error-msg-container {
			background: $alertWarningBackground;
			border: 1px solid $alertWarningBorder;
			box-sizing: border-box;
			border-radius: 4px;
			font-weight: $text-weight-normal;
			font-size: $baseFontSize;
			line-height: 20px;
			padding: 10px;
			margin: 25px;
			color: $app-black;
		}
	}
}

.ember-modal-overlay .ember-modal-dialog.ember-modal-side.agent-availability-modal {

	.ember-modal-header {
		padding: 24px 32px 8px 32px;
	}

	.round-robin-msg {
		margin-bottom: 12px;
		color: $secondary-text-color-1;

		span {
			font-weight: $text-weight-semi-bold;
		}
	}

	.agent-avail-list {

		thead, thead th {
			font-size: 0;
			height: 0;
			opacity: 0;

			//disable select
			pointer-events: none;
			-webkit-touch-callout: none;
			-webkit-user-select: none;
			-moz-user-select: none;
			-ms-user-select: none;
			user-select: none;
		}

		width: 100%;

		tr {
			font-size: 13px;

			th, td {
				border-bottom: 1px solid $border-light-color;
				padding: 12px 4px;
				box-sizing: border-box;
			}

			th.agent-name {
				position: relative;
				text-align: left;

				.avatar__icon {
					position: absolute;
					left: 8px;
					top: 50%;
					transform: translateY(-50%);
				}

				.name {
					margin-left: 48px;
					font-weight: $text-weight-semi-bold;
				}
			}

			td.availability {
				width: 40px;
				margin-top: 5px;
			}
		}
	}
}
